.navbar {
  transition: all .3s linear;
  position: relative;
}
.navbar.navbar-inverse:hover {
  background: #57a524;
}
.navbar .container-fluid {
  padding: 0 40px;
}
.navbar .navbar-right a {
  position: relative;
  padding: 24px 15px;
}
.navbar .navbar-right a i {
  font-size: 16px;
}
.navbar .navbar-right a span {
  background: #ff8400;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  text-align: center;
  line-height: 14px;
  padding: 0;
  position: absolute;
  right: 6px;
  top: 35%;
}
.navbar .nav_con {
  position: relative;
  float: left;
  margin-left: 60px;
}
.navbar .nav_con .line {
  height: 3px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 67px;
  z-index: 9999;
  cursor: pointer;
}
.navbar .nav_con .navbar-nav li a {
  padding: 14.5px 30px;
}
.navbar .nav_con .navbar-nav li a span,
.navbar .nav_con .navbar-nav li a i {
  display: block;
  text-align: center;
}
.navbar .nav_con .navbar-nav li a span {
  font-size: 16px;
  line-height: 24px;
}
.navbar .nav_con .navbar-nav li a i {
  font-size: 10px;
  font-style: normal;
  line-height: 10px;
  margin-top: 7px;
  color: rgba(255, 255, 255, 0.8);
}
.navbar .nav-bg {
  background: rgba(48, 57, 68, 0.95);
  position: absolute;
  width: 100%;
  height: 400px;
  left: 0;
  top: 70px;
  z-index: 1000;
  padding: 30px 0;
  display: none;
}
.navbar .nav-bg .dropdown-list {
  position: relative;
}
.navbar .nav-bg .dropdown-list > li {
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
}
.navbar .nav-bg ul {
  padding: 0;
  margin: 0;
}
.navbar .nav-bg ul li {
  list-style-type: none;
}
.navbar .nav-bg .tab-menu {
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}
.navbar .nav-bg .tab-menu > li {
  list-style-type: none;
}
.navbar .nav-bg .tab-menu > li.active a {
  background: #69c72b;
}
.navbar .nav-bg .tab-menu > li a {
  color: #fff;
  padding: 11px 15px;
  display: block;
}
.navbar .nav-bg .tab-menu > li a i {
  font-size: 20px;
  margin: 0 0 0 10px;
  position: relative;
  top: 2px;
}
.navbar .nav-bg .tab-content ul {
  padding: 0;
  position: relative;
}
.navbar .nav-bg .tab-content ul > li {
  position: absolute;
  left: 0;
  top: 0;
  list-style-type: none;
  display: none;
  width: 100%;
}
.navbar .nav-bg .tab-content ul > li:first-child {
  display: block;
}
.navbar .nav-bg .tab-content ul > li a {
  background: rgba(255, 255, 255, 0.2);
  padding: 15px 20px 12px 20px;
  display: block;
  margin-bottom: 20px;
}
.navbar .nav-bg .tab-content ul > li a:hover {
  background: rgba(255, 255, 255, 0.1);
}
.navbar .nav-bg .tab-content ul > li a h4 {
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  color: #fff;
}
.navbar .nav-bg .tab-content ul > li a h4 span {
  color: #ff8400;
  border: 1px solid #ff8400;
  padding: 0 4px;
  font-size: 12px;
  margin-left: 10px;
}
.navbar .nav-bg .tab-content ul > li a p {
  margin: 3px 0 0 0;
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
}
.container .index-ad {
  margin-top: 10px;
}
.container .index-ad img {
  width: 100%;
}
.container .focus-main {
  margin-top: 20px;
}
.container .focus-main .focus-main-right {
  width: 190px;
  float: right;
  margin-left: 15px;
  background: #fff;
}
.container .focus-main .focus-main-right ul {
  padding: 0;
  margin: 0;
}
.container .focus-main .focus-main-right ul li {
  list-style-type: none;
}
.container .focus-main .focus-main-right ul li:last-child a {
  border-bottom: none;
}
.container .focus-main .focus-main-right ul li.active {
  background: #69c72b;
  position: relative;
}
.container .focus-main .focus-main-right ul li.active:before {
  content: "";
  width: 0;
  height: 0;
  border-width: 12px 12px 12px 0;
  border-style: solid;
  border-color: transparent #69c72b transparent transparent;
  right: 100%;
  top: 50%;
  margin-top: -12px;
  position: absolute;
}
.container .focus-main .focus-main-right ul li.active a {
  border-bottom: none;
  color: #fff;
}
.container .focus-main .focus-main-right ul li a {
  border-bottom: 1px dashed #e8edf1;
  display: block;
  color: #8c9bb1;
  text-align: center;
  padding: 30px 0;
}
.container .focus-main .focus-main-right ul li a i {
  font-size: 42px;
  margin-bottom: 10px;
}
.container .focus-main .focus-main-right ul li a p {
  margin: 0;
}
.container .focus-main .focus-main-left {
  overflow: hidden;
  background: #fff;
  padding: 20px 40px;
}
.container .focus-main .focus-main-left ul {
  padding: 0;
  margin: 0;
  position: relative;
}
.container .focus-main .focus-main-left ul li {
  list-style-type: none;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.container .focus-main .focus-main-left ul li:first-child {
  display: block;
}
.container .focus-main .focus-main-left ul li h1,
.container .focus-main .focus-main-left ul li p {
  text-align: center;
}
.container .focus-main .focus-main-left ul li h1 {
  font-size: 45px;
  margin: 0 0 15px 0;
}
.container .focus-main .focus-main-left ul li h1 span {
  font-size: 24px;
  color: #2d4051;
}
.container .focus-main .focus-main-left ul li p {
  color: #4e617f;
  margin-bottom: 30px;
  font-size: 14px;
}
.container .focus-main .focus-main-left ul li p.notes {
  margin: 15px 0;
  line-height: 20px;
  color: #b8beca;
}
.container .focus-main .focus-main-left ul li p.text-center {
  margin-top: 30px;
}
.container .focus-main .focus-main-left ul li p.text-center .btn {
  color: #ff8400;
  border-color: #ff8400;
}
.container .focus-main .focus-main-left ul li p.text-center .btn:hover {
  background: #ff8400;
}
footer {
  padding: 50px 0 20px 0;
  background: #404b57;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  margin-top: 40px;
}
footer .col-sm-3 {
  padding: 0 40px;
}
@media (max-width: 768px) {
  footer .col-sm-3 {
    padding: 0 15px;
  }
}
footer .col-sm-3 h3 {
  color: #69c72b;
  font-size: 14px;
  padding-bottom: 15px;
  margin: 0 0 15px 0;
  border-bottom: 1px solid #69c72b;
}
footer .copyright {
  margin-top: 40px;
  text-align: center;
}
footer .copyright span {
  margin-right: 15px;
}
footer .copyright i {
  color: #69c72b;
}
footer .copyright h5 {
  margin: 10px 0 20px 0;
}
footer .copyright h5 span {
  color: #69c72b;
  position: relative;
}
footer .copyright h5 span:after,
footer .copyright h5 span:before {
  content: "";
  width: 100px;
  height: 1px;
  background: #69c72b;
  position: absolute;
  top: 10px;
}
footer .copyright h5 span:after {
  right: 110%;
}
footer .copyright h5 span:before {
  left: 110%;
}
footer .copyright .weibo {
  margin: 15px 0;
  font-size: 0;
}
footer .copyright .weibo a {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: #fff;
  margin: 0 5px;
}
footer .copyright .weibo a:first-child {
  background: #55c847;
}
footer .copyright .weibo a:last-child {
  background: #d2423c;
}
footer .copyright .weibo a i {
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  color: #fff;
}
footer .copyright .cpr {
  font-size: 12px;
  margin-top: 10px;
  color: #9499a6;
}
